body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.card {
  color: #333;
  text-align: left;
  background-color: #fff;
  border-radius: 8px;
  max-width: 200px;
  margin: 16px;
  padding: 0;
  transition: transform .3s;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 6px #0000001a;
}

.card:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 12px #00000026;
}

.card .title {
  color: #fff;
  text-align: center;
  background-color: #222;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: bold;
}

.card .illustration {
  width: 100%;
  padding-top: 100%;
  position: relative;
  overflow: hidden;
}

.card .illustration img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.card .description {
  text-align: center;
  background-color: #eee;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  padding: 8px 12px;
  font-size: 12px;
}

.card .performing-actions {
  background-color: #fffc;
  border-radius: 8px;
  align-items: center;
  padding: 16px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.card .performing-actions button, .card .performing-actions input {
  margin: 0 8px;
}

.card .performing-actions input {
  text-align: center;
  width: 50px;
}

.card .performing-actions .cancel-button {
  color: #fff;
  cursor: pointer;
  background-color: #f44336;
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
}

.card .performing-actions .cancel-button:hover {
  background-color: #d32f2f;
}

